.App {
  height: 100vh;
}
.row {
  width: 100vw;
  height: 40%;
}
.row1 {
  width: 100vw;
  height: 60%;
}
.Red {
  /* background-color: red; */
  color: white;
  width: 100%;
  height: 100%;
  overflow: auto;
}
.Green {
  /* background-color: Green; */
  color: white;
  width: 100%;
  height: 100%;
  overflow: auto;
  position: relative;
}
.column {
  width: 100%;
  height: 100%;
  position: relative;
}
.cards {
  display: flex;
  padding: 5px;
  position: relative;

  width: 95%;
  margin: auto;
  align-items: stretch;
}
.card {
  /* margin: 20px; */
  width: 25%;
  padding: 20px;
display: flex;
  position: relative;
  flex:1 1;
  flex-grow: 1;
}
.detailCard {
  display: flex;
  flex-direction: column;
  background-color: #fcf9be;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
    rgba(0, 0, 0, 0.22) 0px 15px 12px;
    flex:1 1;
  flex-grow: 1;
}
.ant-card-body{
  flex: 1 1;
}
.chart {
  width: 80%;
}
.result {
  font-size: 30px;
  font-weight: bold;
  color: green;
}

.ant-card-head-title {
  font-size: 25px;
  color: blue;
}
.titleChart {
  text-align: center;
  font-size: 21px;

  height: -webkit-fit-content;

  height: -moz-fit-content;

  height: fit-content;
  width: 100%;
  margin: auto;
}
canvas {
  height: 100% !important;
}
.nameTittle {
  display: inline;
  background: linear-gradient(to right, #30cfd0 0%, #330867 100%);
  border-radius: 10px;
  color: transparent;
  -webkit-background-clip: text;
          background-clip: text;
  font-weight: 1000;
  font-family: Verdana, Geneva, Tahoma, sans-serif;

  /* color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background: linear-gradient(to right, #FDFBFB, #EBEDEE 70%); */
}
.legendChart {
  font-size: 20px;
}
@media screen and (min-width: 1920px) {
  .detailCard {
    max-height: 100%;
    background-color: #f0e68c;
    font-size: 100%;
  }
  .ant-card-head-title {
    font-size: 20px;
    color: blue;
  }
  .result {
    font-size: 25px;
    font-weight: bold;
    color: green;
  }
  .nameTittle {
    font-size: 20px;
  }
}
@media screen and (min-width: 2560px) {
  .detailCard {
    max-height: 100%;
    background-color: #f0e68c;
    font-size: 200%;
  }

  .ant-card-head-title {
    font-size: 25px;
    color: blue;
  }
  .result {
    font-size: 40px;
    font-weight: bold;
    color: green;
  }
  .nameTittle {
    font-size: 45px;
  }
  .row1 {
    padding-top: 30px;
  }
}
@media screen and (min-width: 3035px) {
  .detailCard {
    max-height: 100%;
    background-color: #f0e68c;
    font-size: 250%;
  }
  .ant-card-head-title {
    font-size: 50px;
    color: blue;
  }
  .result {
    font-size: 60px;
    font-weight: bold;
    color: green;
  }
  .nameTittle {
    font-size: 35px;
  }
}

